    flex-grow: 1;
    height: 30%;
}

.swiper {
    display: flex;
    max-width: 800px;
    width: 100%;
    height: auto;
    max-height: 600px;
    margin: 0 auto;
    margin-top: 4%;
}

.swiper-wrapper {
    box-sizing: content-box;
}

.swiper-slide{
    display: flex;
}

.swiper-slide img {
    width: 100%;
    height: 100%;
}


.swiper-button-prev::after, .swiper-button-next::after{
    font-size: 30px;
}

.container {
    margin-top: 5%;
}

