import './../style/style.css';
import './../style/swiper-bundle.min.css';
import './../style/bootstrap.min.css';
import Swiper from 'swiper';
import {Navigation, Pagination, Autoplay} from 'swiper';

const swiper = new Swiper('.swiper', { 
    direction: 'horizontal',
    loop: true,
    slidesPerView: 1,
    
    pagination: {
        el: '.swiper-pagination',
        clickable: true,
    }, 

    navigation: {
        nextEl: '.swiper-button-next',
        prevEl: '.swiper-button-prev',
    },

     autoplay: {
         delay: 1000,
     },
});

$(document).ready(function () {
    var resultSum = 0;
    
    $("#type_deposit").change(function () {
        var val = $(this).val();
        if (val == " ") {
            $("#long_deposit").html("<option> </option>");
        }
        else if (val == "") {
            $("#long_deposit").html("<option value='20'>6  - 20%</option> <option value='22'>1  - 22%</option> <option value='15'>1,5  - 15%</option> <option value='10'>2  - 10%</option>");
        } else if (val == "") {
            $("#long_deposit").html("<option value='20'>3  - 20%</option> <option value='22'>6  - 22%</option> <option value='23'>9  - 23%</option> <option value='24'>1  - 24%</option> <option value='18'>1,5  - 18%</option> <option value='15'>2  - 15%</option>");
        }
    });
    
    $("#calc").click(function () {
       
        var sum = $('#sum').val();
        
        var type = $('#type_deposit').val();
        var long = $('#long_deposit').val();
        if (type == "") {
            resultSum = Number(sum) + (sum*(long)*0.01); // 0.01 - ,  
        }
        else if (type == "") {
            resultSum = Number(sum) + (sum*(long)*0.015); // 0.015 - ,  
        }
        document.getElementById('result').innerHTML = (" " + type + "   " + $('#long_deposit option:selected').text() + "   " + sum + " ." + "<br>     " + resultSum + " .");  
    });

});
